import React from "react";
import {MDBPopover, MDBPopoverBody, MDBBtn, MDBIcon, MDBContainer} from "mdbreact";

const PopoverPage = props => {
    return (
        <MDBContainer>
            <MDBPopover
                placement="top"
                popover
                clickable
                id="popper5"
            >
                <MDBBtn color="blue"><p>click camera</p><MDBIcon icon="camera-retro" size="2x"/></MDBBtn>
                <div>
                    <MDBPopoverBody>
                        <img src="data:image/jpeg;base64," alt="" />
                    </MDBPopoverBody>
                </div>
            </MDBPopover>

        </MDBContainer>
    );
}

export default PopoverPage;